@import url('https://fonts.googleapis.com/css2?family=Karla:wght@600;700&family=Lato:wght@400;700;900&family=Open+Sans:wght@300;700&display=swap');

* {
    box-sizing: border-box;
}

body, html {
    font-family: 'Karla', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #9a8c98;
}

.hero {
    text-align: center;
    background-color: #222222;
    color: white;
    font-size: 32px;
    padding: 20px;
    margin-bottom: 10px;
}

.personal-info {
    display: flex;
    justify-content: center;
    align-items: center;
}

.form--personal {
    width: 50%;
    display: flex;
    margin: 10px;
}

.form {
    width: 100%;
    background-color: #c9ada7;
    border-radius: 5px;
    padding: 40px 60px;
    /* border: 1px solid magenta; */
}

.form > input, h3 {
    width: 100%;
    display: flex;
    padding: 12px;
    margin: 10px 0px;
    border: none;
    border-radius: 5px;
}

.form > h3 {
    border: 3px solid black;
    background-color: white;
    margin: 0px;
    font-size: 20px;
}

.form input:hover {
    border: 1px solid black;
}

.cvp--container {
    background-color: #cccccc;
    width: 50%;
    height: 900px;
    margin: 0 auto;
}

.personal__info {
    background-color: #3a0ca3;
    height: 15%;
    color: white;
    padding: 0px;
}

.personal__info > h1 {
    padding: 10px;
    margin-bottom: 0px
}

.profile-pic {
    border: 2px solid black;
    width: 80%;
    height: 200px;
}

.info--container {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    width: 100%;
}

.info--container  p {
    padding: 5px 10px;
}

.main--info {
    width: 100%;
    height: 100vh;
    background-color: #fff;
    padding: 20px;
}

.address--info {
    width: 40%;
    height: 100vh;
    background-color: #e5e5e5;
    padding: 30px;
}

hr {
    border: 0;
    height: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.address--info > h3 {
    padding: 0;
} 

.address--info__para {
    font-size: 16px;
}